.section{
  padding: 94px 0 34px;
}
.recipe {
  display: flex;
  flex-direction: column;
  gap: 50px;
  margin-bottom: 34px;
  padding: 0 32px;
  &__title-name {
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    line-height: 1.16em;
    letter-spacing: -0.72px;
    font-family: $font;
    margin: 0;
  }
  &__title-full {
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    line-height: 1.16em;
    letter-spacing: -0.72px;
    color: $primary-color;
  }
  &__block {
    display: flex;
    gap: 20px;
  }
  &__media {
    position: relative;
  }
  &__video {
    width: 600px;
    height: 400px;
    border-radius: 15px;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%),
      url("/src/images/recipe-images/recipe.png"), lightgray -10.819px -343.839px / 103.473% 236.295% no-repeat;
  }
  &__text {
    display: flex;
    flex-direction: column;
    gap: 28px;
    padding: 32px;
    margin: 0;
    font-family: $font;
  }
  &__commit {
    color: $text-color;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.33em;
    word-wrap: break-word;
    width: 350px;
    margin: 0;
  }
}
.recipe__play {
  width: 44px;
  height: 44px;
  position: absolute;
  fill: $background-primary;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  &:hover {
    cursor: pointer;
    fill: $primary-color;
  }
}
.recipe__steps {
  display: flex;
  width: 452px;
  flex-direction: column;
  gap: 24px;
  margin: 0;
  padding: 0;
  align-items: flex-start;
  padding-left: 16px;
  &__element {
    color: $text-color;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.28em;
    letter-spacing: -0.28px;
    text-align: start;
    list-style: disc;
  }
}


// resposive for phones
@media (max-width: 375px) {
  .recipe{
    padding: 0 20px;
    margin-bottom: 0;
    &__block{
      display: block;
    }
    &__video {
      width: 335px;
      height: 500px;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%),
      url("/src/images/recipe-images/recipe2xPhone.jpg"), lightgray -10.819px -343.839px / 103.473% 236.295% no-repeat;
    }
    &__text{
      padding: 20px 14px;
    }
    &__commit{
      width: auto;
    }
    &__steps{
      width: auto;
    }
  }
}

  // responsive for tablets
@media (min-width: 376px) and (max-width: 768px) {
  .recipe{
    padding: 0 77px;
    margin-bottom: 0;
    &__block{
      display: block;
      text-align: center;
    }
    &__video {
      margin-bottom: 20px;
      width: 614px;
      height: 400px;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%),
      url("/src/images/recipe-images/recipeTablet.jpg"), lightgray -10.819px -343.839px / 103.473% 236.295% no-repeat;
    }
    &__text{
      padding: 32px;
    }
    &__commit{
      width: 350px;
      text-align: left;
    }
    &__steps{
      width: auto;
    }
  }
  .section{
    padding-bottom: 6px;
  }
}