.list {
  list-style: none;
}
.flavor {
  padding: 128px 32px 34px 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  &__legend {
    display: flex;
    width: 486px;
    flex-direction: column;
    align-items: center;
    gap: 28px;
    flex-shrink: 0;
    text-align: center;
  }
  &-title__container {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  &__title {
    /*display: flex;*/
    color: $text-color;
    font-weight: 800;
    font-size: 24px;
    padding-left: 24px;
    padding-right: 24px;
    font-family: $font;
    margin: 0;
  }
  &__description {
    color: $text-color;
    text-align: center;
    font-family: $font;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px; /* 128.571% */
    letter-spacing: -0.28px;
  }
  &__list {
    font-family: $font;
    text-align: center;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    border-top: 50px;
    align-items: center;
    gap: 19px;
    padding: 0%;
  }
  &-item__container {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
  }
  &__image {
    padding-bottom: 36px;
  }
  &__item {
    width: 270px;
    height: 270px;
    position: relative;
    overflow: hidden;
    display: flex;
    padding: 28px 36px 28px 28px;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 36px;
    border-radius: 15px;
    background: $font-color;
    color: $text-color;
    text-transform: uppercase;
    border: 1px solid;
    border-color: $primary-color;
  }
  &__item--first {
    background-color: $primary-color;
    color: $font-color;
  }
  &__item--second {
    background-color: rgba(30, 24, 35, 1);
    color: $font-color;
    border-color: none;
  }
}
.flavor__text {
  padding-right: 25%;
}
.flavor-title--orange {
  color: $primary-color;
}
.deslizable {
  font-family: $font;
  position: absolute;
  top: 100%;
  left: 0%;
  background-color: $font-color-bg;
  color: $text-color;
  margin: 0%;
  padding: 20px;
  padding-top: 0%;
  height: 100%;
  text-align: left;
  transition: top 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &__head {
    color: $text-color;
    font-family: $font;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: -0.54px;
    text-transform: uppercase;
  }
  &__container {
    display: flex;
    align-items: flex-start;
    gap: 6px;
  }
  &__title {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 35px;
    text-transform: lowercase;
    min-width: 85px;
    height: 24px;
    background-color: $primary-color;
    color: $font-color;
    font-family: $font;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px; /* 120% */
    padding: 6px 16px;
  }
  &__sub {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 35px;
    text-transform: lowercase;
    min-width: 85px;
    height: 24px;
    border: 1px solid $primary-color;
    color: $text-color;
    font-family: $font;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px; /* 120% */
    padding: 6px 16px;
  }
  &__description {
    overflow: hidden;
    color: $text-color;
    text-overflow: ellipsis;
    text-transform: capitalize;
    font-family: $font;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px; /* 128.571% */
    letter-spacing: -0.28px;
  }
}

.flavor__item:hover .deslizable {
  top: 0%;
}
@media (max-width: 768px) {
  .flavor__title {
    max-width: 411px;
    text-align: center;
    font-family: $font;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px; /* 120% */
    letter-spacing: -0.6px;
    text-transform: uppercase;
  }
  .flavor__description {
    max-width: 486px;
    text-align: center;
    font-family: $font;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px; /* 128.571% */
    letter-spacing: -0.28px;
  }
  .flavor__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .flavor__item {
    width: 336px;
    display: flex;
    align-items: center;
  }
  .flavor-item__container {
    display: flex;
    flex-direction: column;
  }
  .flavor__image {
    display: flex;
    align-self: center;
  }
}
@media (max-width: 375px) {
  .flavor__title {
    max-width: 283px;
    text-align: center;
    font-family: $font;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px; /* 120% */
    letter-spacing: -0.6px;
    text-transform: uppercase;
  }
  .flavor__description {
    max-width: 335px;
    text-align: center;
    font-family: $font;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px; /* 128.571% */
    letter-spacing: -0.28px;
  }
  .flavor__list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-self: center;
    max-width: 335px;
  }
  .flavor__item {
    width: 335px;
  }
}
